import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { renderFieldSelect } from "../../renderField";
import validasiUser from "../../Validations/validasiUser";

const renderField = ({
  input,
  label,
  type,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const maptoState = (state) => {
  return {
    initialValues: {
      namaLengkap: state.provinsi.editUser.namaUser,
      userID: state.provinsi.editUser.idUser,
      password: state.provinsi.editUser.password,
      rePassword: state.provinsi.editUser.password,
      level: state.provinsi.editUser.level,
    },
  };
};
class ForAddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listProvinsi: [],
    };
  }

  render() {
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div
              className={
                this.props.changePassword ? "col-lg-4 d-none" : "col-lg-4"
              }
            >
              <Field
                name="namaLengkap"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Nama Lengkap"
              />
            </div>
            <div className={!this.props.edit ? "col-lg-4 " : "col-lg-4 d-none"}>
              <Field
                name="userID"
                type="text"
                component={renderField}
                label="User ID"
              />
            </div>
            <div
              className={
                this.props.changePassword ? "col-lg-4 d-none" : "col-lg-4"
              }
            >
              <Field
                component={renderFieldSelect}
                label="Level"
                name="level"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                className="form-control"
                options={[
                  { value: "ADM", name: "ADMIN" },
                  { value: "OWN", name: "OWNER" },
                  { value: "MRK", name: "MARKETING" },
                  { value: "SPV", name: "SUPERVISOR" },
                ]}
              ></Field>
            </div>
            {this.props.changePassword ? (
              <div className="col-lg-4">
                <Field
                  name="passwordLama"
                  type="password"
                  component={renderField}
                  label="Password Lama"
                />
              </div>
            ) : null}
            {!this.props.edit ? (
              <>
                <div className="col-lg-4">
                  <Field
                    name="password"
                    type="password"
                    component={renderField}
                    label="Password"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="rePassword"
                    type="password"
                    component={renderField}
                    label="Retype Password"
                  />
                </div>
              </>
            ) : this.props.changePassword ? (
              <>
                <div className="col-lg-4">
                  <Field
                    name="password"
                    type="password"
                    component={renderField}
                    label="Password Baru"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="rePassword"
                    type="password"
                    component={renderField}
                    label="Retype Password Baru"
                  />
                </div>
              </>
            ) : null}
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.submitting}
                >
                  Selesai
                  <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

ForAddUser = reduxForm({
  form: "AddUser", // a unique identifier for this form
  enableReinitialize: true,
  validate: validasiUser,
})(ForAddUser);
export default connect(maptoState, null)(ForAddUser);
