import React, { Component } from "react";
import addUser from "../Images/AddUser2.svg";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import FormAddUser from "./Component/FormAddUser";
import { getDatauser, getUserData } from "../actions/userAction";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { berhasil, gagal } from "../Alert";
import { getUUID } from "../actions/fakturAction";
import ModalMasterData from "./Component/ModalMasterData";
import { hideModal, showModal } from "../actions/ModalAction";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
  return {
    listDataUser: state.provinsi.listDataUser,
  };
};

class AddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "user_id",
          text: "ID User",
        },
        {
          dataField: "user_name",
          text: "Nama Pegawai",
          headerStyle: () => {
            return { width: "20%" };
          },
        },
        {
          dataField: "level",
          text: "Level",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  data-target="#modalTambahUser"
                  data-toggle="modal"
                  onClick={() =>
                    this.props.dispatch(
                      getUserData(
                        row.user_id,
                        row.user_name,
                        row.level,
                        row.password,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>

                <button
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    AxiosMasterDelete("users/delete/id/" + row.user_id)
                      .then(() => berhasil("Akun Berhasil Dihapus"))
                      .then(() => this.props.dispatch(getDatauser()))
                      .catch(() =>
                        gagal(
                          "sepertinya ada kesalahan, silahkan check koneksi anda"
                        )
                      )
                  }
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "20%" };
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getDatauser());
  }

  handleSubmit(data) {
    var simpan = {
      user_name: data.namaLengkap,
      password: data.password,
      retype_password: data.password,
      user_id: data.userID,
      level: data.level,
    };

    AxiosMasterPost("users/add/user", simpan)
      .then(() => this.props.dispatch(hideModal()))
      .then(() =>
        berhasil(
          "User berhasil disimpan",
          this.props.dispatch(getUUID(), this.props.dispatch(getDatauser()))
        )
      )
      .catch(() =>
        gagal("Sepertinya ada gangguan, mohon periksa koneksi anda")
      );
  }
  handleSubmitEdit(data) {
    var simpan = {
      user_name: data.namaLengkap,
      level: data.level,
    };
    AxiosMasterPut("/users/update/id/" + data.userID, simpan)
      .then(() => this.props.dispatch(hideModal()))
      .then(() =>
        berhasil(
          "User berhasil dirubah",
          this.props.dispatch(getUUID(), this.props.dispatch(getDatauser()))
        )
      )
      .catch(() =>
        gagal("Sepertinya ada gangguan, mohon periksa koneksi anda")
      );
  }

  editModal() {
    this.props.dispatch(showModal());
    this.props.dispatch(getUserData());
    this.setState({ isEdit: true, changePassword: false });
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }
  changePassword() {
    this.props.dispatch(showModal());
    this.props.dispatch(getUserData());
    this.setState({ isEdit: true, changePassword: true });
  }

  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/manageUser">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="card">
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12">
                <div className="text-center">
                  <img src={addUser} alt="Add User Logo" width={200} />
                  <p>
                    Silahkan Simpan ID dan password baik baik, jangan password
                    <br />
                    dengan kata umum, seperti: Tanggal lahir, atau nama
                  </p>
                </div>
              </div>
              <ModalMasterData
                content={
                  this.state.isEdit ? (
                    this.state.changePassword ? (
                      <FormAddUser
                        onSubmit={(data) => this.handleSubmitEdit(data)}
                        edit={true}
                        changePassword={true}
                      />
                    ) : (
                      <FormAddUser
                        onSubmit={(data) => this.handleSubmitEdit(data)}
                        edit={true}
                      />
                    )
                  ) : (
                    <FormAddUser onSubmit={(data) => this.handleSubmit(data)} />
                  )
                }
                title={
                  this.state.isEdit ? "Edit Data User" : "Tambah Data User"
                }
              />
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    onClick={() => this.tambahModal()}
                  >
                    Tambah User
                  </button>
                </div>
              </div>
              <div className="col-lg-12">
                {this.props.listDataUser ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.listDataUser}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : null}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(AddUser);
