const validasiUser = (value) => {
  const errors = {};
  if (!value.password) {
    errors.password = "Tidak Boleh kosong";
  } else if (value.password.length < 6) {
    errors.password = "Password kurang dari 6";
  }
  if (!value.rePassword) {
    errors.rePassword = "Tidak Boleh kosong";
  } else if (value.rePassword.length < 6) {
    errors.rePassword = "Password kurang dari 6";
  } else if (value.rePassword !== value.password) {
    errors.rePassword = "Password tidak sama";
  }
  if (!value.namaLengkap) {
    errors.namaLengkap = "Tidak Boleh kosong";
  }
  if (!value.level) {
    errors.level = "Tidak Boleh kosong";
  }
  return errors;
};

export default validasiUser;
